import React from "react";
import styled from "styled-components";

const Roaster = styled.ul`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  max-width: 1080px;
  margin: 20px auto 0;
`;

const Roaster__item = styled.li`
  display: flex;
  flex-direction: column;
  width: 200px;
  margin: 10px 5px 0;
  border: solid 1px #ccc;
  border-radius: 3px;
  transform: scale(1);
  transition: 0.3s ease-out;
  &:hover {
    transform: scale(1.02);
    transition: 0.3s ease-out;
  }
`;

const Roaster__item__imgBlock = styled.div`
  width: 200px;
  height: 200px;
`;

const Roaster__item__img = styled.img`
  width: 100%;
  object-fit: cover;
  height: 100%;
`;

const Roaster__item__name = styled.p`
  text-align: center;
  padding: 4px 0;
  letter-spacing: 1px;
`;

const MemberList = props => {
  return (
    <Roaster className="roster">
      {props.group.item_list.map((row, index) => {
        return (
          <Roaster__item key={index}>
            <Roaster__item__imgBlock>
              <Roaster__item__img src={row.photo_url} alt="aaa" />
            </Roaster__item__imgBlock>
            <Roaster__item__name>{row.user_name}</Roaster__item__name>
          </Roaster__item>
        );
      })}
    </Roaster>
  );
};

export default MemberList;
